import React, {Component, Fragment} from "react";
import {animateScroll as scroll} from "react-scroll";
import {IoIosArrowDropdown, IoIosArrowDropup} from "react-icons/io";
import DropUI from "../../layout/UI/DropUI";
import {connect} from "react-redux";
import Dropzone from 'react-dropzone';
import "react-dropzone/examples/theme.css";
import {IoIosClose, IoIosPerson, IoIosArrowDroprightCircle, IoIosArrowDropleftCircle, IoMdAttach} from "react-icons/io";
import {
    Badge,
    Button,
    Form, FormInput, FormGroup
} from "shards-react";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import {fetchAllAttribute, fetchUserfollowings, fetchUserSession, setFormData} from "../../actions/index";
import Modal from 'react-responsive-modal';
import AutoComplete from '../../layout/UI/AutoComplete';
import AutoCompleteAttribute from "../../layout/UI/AutoCompleteAttribute";
import toast from 'toasted-notes'
import 'toasted-notes/src/styles.css';
import {IoMdCloseCircle, IoMdCheckmarkCircle} from "react-icons/io";


class StepTwo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            alternative: [],
            attributes: [],
            participants: [],
            openAlterModal: false,
            openAttrModal: false,
            openPartModal: false,
            openAttachModal: false,
            text: '',
            alterTemText: "",
            attrTempText: "",
            partiTempText: "",
            attachmentTempText: "",
            files: [],
            validAlternative: false,
            validAttributes: false,
            validParticipants: false,
        };
        this.onDrop = (files) => {
            if (files.length >= 2) {
                return null;
            }
            this.setState({
                files: [...this.state.files, ...files]
            })
        };
        this.modules = {
            toolbar: [
                [{'header': [1, 2, false]}],
                ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
                ['link'],
                ['clean']
            ],
        };
        this.formats = [
            'header',
            'bold', 'italic', 'underline', 'strike', 'blockquote',
            'list', 'bullet', 'indent',
            'link', 'image'
        ]

    }

    toggle = () => {
        this.setState({
            openAlterModal: !this.state.openAlterModal
        })
    };

    async componentDidMount() {
        scroll.scrollToTop();
        const {dispatch} = this.props;
        if (this.props.sessionID === undefined) {
            await dispatch(fetchUserSession())
        }
        dispatch(fetchUserfollowings(this.props.sessionID)); //TODO parameter is hardcoded !
        dispatch(fetchAllAttribute());

    };


    showErrorMessage = (text) => toast.notify(
        <p className={"m-0 p-0 font-size-14"}>
            <span className={"mr-2 font-size-19"} style={{verticalAlign: "1px"}}>
                <IoMdCloseCircle style={{color: "#f5222d"}}/>
            </span>
            {text}
        </p>
        , {
            duration: 4000
        });

    showSuccessMessage = (text, type) => toast.notify(
        <p className={"m-0 p-0 font-size-14"}>
            <span className={"mr-2 font-size-19"} style={{verticalAlign: "1px"}}>
                    <IoMdCheckmarkCircle style={{color: "#52c41a"}}/>
            </span>
            {text}
        </p>
        , {
            duration: 4000
        });

    handleChange = (value) => {
        this.setState({text: value});
    };

    removeAlternative = (alterName) => {
        let newAlternative = this.state.alternative.filter(item => {
            return item["name"] !== alterName;
        });
        this.setState({
            alternative: newAlternative
        })
    };

    _renderAlternative = () => {
        let items = this.state.alternative.map((item, index) => {
            return (<div className={"select-attribute-item my-2 d-flex justify-content-between"} key={index}>
                <p className={"m-0 p-0 d-flex align-items-center"} style={{fontWeight: "300"}}>
                    {item["name"]}
                </p>
                <Button outline pill theme="warning" size={"sm"}
                        className={"btn btn-success-custom p-0 m-0"}>
                    <IoIosClose className={"font-size-22"} onClick={() => this.removeAlternative(item["name"])}/>
                </Button>
            </div>)
        });
        return items
    };

    removeParticipant = (text) => {
        let newParticipant = this.state.participants.filter(item => {
            return item !== text;
        });
        this.setState({
            participants: newParticipant
        })
    };

    _renderParticipant = () => {
        let items = this.state.participants.map((item, index) => {
            return (
                <div className={"select-attribute-item my-2 d-flex justify-content-between"} key={index}>
                    <p className={"m-0 p-0 d-flex align-items-center"} style={{fontWeight: "300"}}>
                        <IoIosPerson className={"mr-2 text-orange font-size-20"}/>
                        {item}
                    </p>
                    <Button outline pill theme="warning" size={"sm"}
                            className={"btn btn-success-custom p-0 m-0"}>
                        <IoIosClose className={"font-size-22"} onClick={() => this.removeParticipant(item)}/>
                    </Button>
                </div>)
        });
        return items
    };

    removeAttachment = (text) => {
        let newAttachment = this.state.files.filter(item => {
            return item["name"] !== text;
        });
        this.setState({
            files: newAttachment
        })
    };

    _renderAttachments = () => {
        let items = this.state.files.map((item, index) => {
            return (
                <div className={"select-attribute-item my-2 d-flex justify-content-between"} key={index}>
                    <p className={"m-0 p-0 d-flex align-items-center"} style={{fontWeight: "300"}}>
                        <IoMdAttach className={"mr-2 text-orange font-size-20"}/>
                        {item["name"]}
                    </p>
                    <Button outline pill theme="warning" size={"sm"}
                            className={"btn btn-success-custom p-0 m-0"}>
                        <IoIosClose className={"font-size-22"} onClick={() => this.removeAttachment(item["name"])}/>
                    </Button>
                </div>)
        });
        return items
    };

    removeAttribute = (attrName) => {
        let newAttribute = this.state.attributes.filter(item => {
            return item !== attrName;
        });
        this.setState({
            attributes: newAttribute
        })
    };

    _renderAttributes = () => {
        let items = this.state.attributes.map((item, index) => {
            return (
                <Badge className={"select-attribute-tag my-1"} pill key={index}>
                                    <span className={"ml-2 my-3"}>
                                        {item}
                                    </span>
                    <Button pill theme="light" size={"sm"}
                            className={"p-0 m-0 ml-2"}>
                        <IoIosClose className={"font-size-19 text-orange"} onClick={() => this.removeAttribute(item)}/>
                    </Button>
                </Badge>)
        });
        return items
    };

    addData = async () => {
        if (this.state.alterTemText === "") {
            this.showErrorMessage("The filed should not be empty");
            return null
        }
        let alterExist = this.state.alternative.filter(item => item.name === this.state.alterTemText);
        if (alterExist.length > 0) {
            this.showErrorMessage("The Alternative Already added!");
            return null
        }
        await this.setState({
            alternative: [
                ...this.state.alternative,
                {
                    name: this.state.alterTemText,
                    description: this.state.text
                }
            ],
            openAlterModal: !this.state.openAlterModal,
            text: '',
        });
        this.setState({
            validAlternative: (this.state.alternative.length >= 2 && this.state.alternative.length <= 5)
        });
    };

    toggleAttr = () => {
        this.setState({
            openAttrModal: !this.state.openAttrModal
        })
    };

    addDataAttr = async () => {
        if (this.state.attrTempText === "") {
            this.showErrorMessage("The filed should not be empty");
            return null
        }
        if (this.state.attributes.indexOf(this.state.attrTempText) !== -1) {
            this.showErrorMessage("The Attribute Already added!");
            return null
        }
        await this.setState({
            attributes: [
                ...this.state.attributes,
                this.state.attrTempText
            ],
            openAttrModal: !this.state.openAttrModal,
            attrTempText: ""
        });
        this.setState({
            validAttributes: (this.state.attributes.length >= 3 && this.state.attributes.length <= 5)
        });
    };

    _renderAlternativeModal = () => {
        return (
            <Modal open={this.state.openAlterModal} onClose={this.toggle} center>
                <div>
                    <h3>Add Alternative</h3>
                    <hr/>
                    <div>
                        <Form>
                            <FormGroup>
                                <label htmlFor="#AlternativeName">Alternative Name</label>
                                <FormInput id="#AlternativeName"
                                           onChange={(e) => this.setState({alterTemText: e.target.value})}
                                           placeholder="Alternative Name"/>
                            </FormGroup>
                        </Form>
                        <p>Detailed Alternative description :</p>
                        <ReactQuill value={this.state.text}
                                    modules={this.modules}
                                    formats={this.formats}
                                    onChange={this.handleChange}/>

                    </div>
                    <hr/>
                    <div>
                        <Button theme={"primary"} onClick={this.addData}>
                            OK
                        </Button>
                    </div>
                </div>
            </Modal>
        )
    };

    onChangeAttribute = (event, {newValue}) => {
        this.setState({
            attrTempText: newValue
        });
    };

    _renderAttributeModal = () => {
        const {openAttrModal} = this.state;
        return (
            <Modal open={openAttrModal} onClose={this.toggleAttr} center>
                <div className={"modal-responsive"}>
                    <h3>
                        Add Attribute
                    </h3>
                    <hr/>
                    <div>
                        <Form>
                            <FormGroup>
                                <label htmlFor="#AttributeName">Attribute Name</label>
                                <AutoCompleteAttribute
                                    inputProps={{
                                        placeholder: 'Attributes',
                                        value: this.state.attrTempText,
                                        onChange: this.onChangeAttribute
                                    }}
                                    data={this.props.allAttributes["data"]["results"]}/>
                            </FormGroup>
                        </Form>
                    </div>
                    <hr/>
                    <div>
                        <Button theme={"primary"} onClick={this.addDataAttr}>
                            OK
                        </Button>
                    </div>
                </div>
            </Modal>
        )
    };

    toggleParticipant = () => {
        this.setState({
            openPartModal: !this.state.openPartModal
        })
    };

    toggleAttachment = () => {
        this.setState({
            openAttachModal: !this.state.openAttachModal
        })
    };

    addDataParticipant = async () => {
        if (this.state.partiTempText === "") {
            this.showErrorMessage("The filed should not be empty");
            return null
        }
        if (this.state.participants.indexOf(this.state.partiTempText) !== -1) {
            this.showErrorMessage("The user Already added!");
            return null
        }
        await this.setState({
            participants: [
                ...this.state.participants,
                this.state.partiTempText
            ],
            openPartModal: !this.state.openPartModal,
            partiTempText: ""
        });
        this.setState({
            validParticipants: (this.state.participants.length >= 3 && this.state.participants.length <= 5)
        });
    };

    onChangeParticipant = (event, {newValue}) => {
        this.setState({
            partiTempText: newValue
        });
    };

    _renderParticipantModal = () => {
        const {openPartModal} = this.state;
        return (
            <Modal open={openPartModal} onClose={this.toggleParticipant} center>
                <div className={"modal-responsive"}>
                    <h3>
                        Add Participant
                    </h3>
                    <hr/>
                    <div>
                        <Form>
                            <FormGroup>
                                <label htmlFor="#ParticipantName">Participant Name</label>
                                <AutoComplete

                                    inputProps={{
                                        placeholder: 'Participant',
                                        value: this.state.partiTempText,
                                        onChange: this.onChangeParticipant
                                    }}
                                    data={this.props.userFollowings["data"]}/>
                            </FormGroup>
                        </Form>
                    </div>
                    <hr/>
                    <div>
                        <Button theme={"primary"} onClick={this.addDataParticipant}>
                            OK
                        </Button>
                    </div>
                </div>
            </Modal>
        )
    };

    addDataAttachment = () => {
        this.setState({
            // attachments: [
            //     ...this.state.attachments,
            //     this.state.attachmentTempText
            // ],
            openAttachModal: !this.state.openAttachModal
        })
    };

    onDrop = (files) => {

    };

    _renderAttachmentModal = () => {
        const {openAttachModal} = this.state;
        const files = this.state.files.map(file => (
            <li key={file.name}>
                {file.name} - {file.size} bytes
            </li>
        ));
        return (
            <Modal open={openAttachModal} onClose={this.toggleAttachment} center>
                <div className={"modal-responsive"}>
                    <h3>
                        Add Attachment
                    </h3>
                    <hr/>
                    <div>
                        <Dropzone onDrop={this.onDrop}>
                            {({getRootProps, getInputProps}) => (
                                <section className="container">
                                    <div {...getRootProps({className: 'dropzone'})}>
                                        <input {...getInputProps()} />
                                        <p>Drag 'n' drop some files here, or click to select files</p>
                                    </div>
                                    <aside>
                                        <h4>Files</h4>
                                        <ul>{files}</ul>
                                    </aside>
                                </section>
                            )}
                        </Dropzone>

                    </div>
                    <hr/>
                    <div>
                        <Button theme={"primary"} onClick={this.addDataAttachment}>
                            OK
                        </Button>
                    </div>
                </div>
            </Modal>
        )
    };

    checkFormValidation = () => {
        if (!this.state.validAlternative) {
            this.showErrorMessage("Number of Alternatives should be between 2 and 5 !");
        }
        if (!this.state.validAttributes) {
            this.showErrorMessage("Number of Attributes should be between 3 and 5 !");
        }
        if (!this.state.validParticipants) {
            this.showErrorMessage("Number of Participants should be between 3 and 5 !");
        }
        if (this.state.validParticipants && this.state.validAttributes && this.state.validAlternative) {
            return this.checkValidation()
        }
        return null
    };

    checkValidation = () => {

        this.props.dispatch(setFormData({
            "Alternatives": this.state.alternative,
            "requiredAttributes": this.state.attributes,
            "Participants": this.state.participants,
            "Attachments": this.state.files
        }));
        return this.props.goToNextSlide()
    };

    render() {
        return (
            <Fragment>
                {this.props.activeKey === this.props.tabKey ?
                    (
                        <Fragment>
                            {this._renderAlternativeModal()}
                            {this._renderAttributeModal()}
                            {this._renderParticipantModal()}
                            {this._renderAttachmentModal()}

                            <div className={"colored-bar-step d-flex flex-column"}>
                                <div className={"mt-3 text-white"}>
                                    <IoIosArrowDropup className={"form-ui-icon"} onClick={this.props.goToPrevSlide}/>
                                </div>
                                <div className={"d-flex flex-column align-items-center"}>
                                    {this.props.dotClass.map((item, index) => {
                                        return <DropUI isActive={index === this.props.index} key={index}/>
                                    })}
                                </div>
                                <div className={"mb-3 text-white"}>
                                    <IoIosArrowDropdown className={"form-ui-icon"} onClick={this.props.goToNextSlide}/>
                                </div>
                            </div>
                            <div style={{"flexGrow": "1"}} className={"mb-3"}>
                                <div className={"form-ui-header p-3"}>
                                    <h3 className={"m-0 p-0"}>
                                        Set Your Alternatives, Attributes and Participant
                                    </h3>
                                </div>

                                <div className={"form-ui p-3"}>
                                    <h5 className={"m-0"}>
                                        Alternatives
                                        {/*<span style={{*/}
                                        {/*color: "red",*/}
                                        {/*display: this.state.invalidAlternative ? "inline" : "none"*/}
                                        {/*}}>*</span>*/}
                                    </h5>
                                    <div className={"select-attribute-wrapper"}>
                                        <div className={"select-attribute-body"}>
                                            {this.state.alternative.length > 0 ? null : (
                                                <div className={"d-flex justify-content-center"}>
                                                    <p style={{fontWeight: 100}} className={"my-4"}>
                                                        Add your Alternative
                                                    </p>
                                                </div>
                                            )}
                                            {this._renderAlternative()}
                                        </div>
                                        <div className={"select-attribute-footer mt-2 d-flex justify-content-end"}>
                                            <Button theme="primary" onClick={this.toggle}>Add</Button>
                                        </div>
                                    </div>
                                </div>
                                <hr className={"m-0"}/>
                                <div className={"form-ui px-3 mt-3 pb-3"}>
                                    <h5 className={"m-0"}>
                                        Required Attributes
                                        {/*<span style={{*/}
                                        {/*color: "red",*/}
                                        {/*display: this.state.invalidAttributes ? "inline" : "none"*/}
                                        {/*}}>*</span>*/}
                                    </h5>
                                    <div className={"select-attribute-wrapper"}>
                                        <div className={"select-attribute-body"}>
                                            {this.state.attributes.length > 0 ? null : (
                                                <div className={"d-flex justify-content-center"}>
                                                    <p style={{fontWeight: 100}} className={"my-4"}>
                                                        Add your Attributes
                                                    </p>
                                                </div>
                                            )}
                                            <div className={"d-flex flex-column align-items-start"}>
                                                {this._renderAttributes()}
                                            </div>
                                        </div>
                                        <div className={"select-attribute-footer mt-2 d-flex justify-content-end"}>
                                            <Button theme="primary" onClick={this.toggleAttr}>Add</Button>
                                        </div>
                                    </div>
                                </div>
                                <hr className={"m-0"}/>
                                <div className={"form-ui p-3"}>
                                    <h5 className={"m-0"}>
                                        Participants
                                        {/*<span style={{*/}
                                        {/*color: "red",*/}
                                        {/*display: this.state.invalidParticipants ? "inline" : "none"*/}
                                        {/*}}>*</span>*/}
                                    </h5>
                                    <div className={"select-attribute-wrapper"}>
                                        <div className={"select-attribute-body"}>
                                            {this.state.participants.length > 0 ? null : (
                                                <div className={"d-flex justify-content-center"}>
                                                    <p style={{fontWeight: 100}} className={"my-4"}>
                                                        Add your Participants
                                                    </p>
                                                </div>
                                            )}
                                            {this._renderParticipant()}
                                        </div>
                                        <div className={"select-attribute-footer mt-2 d-flex justify-content-end"}>
                                            <Button theme="primary" onClick={this.toggleParticipant}>Add</Button>
                                        </div>
                                    </div>
                                </div>
                                <hr className={"m-0"}/>
                                <div className={"form-ui p-3"}>
                                    <h5 className={"m-0"}>
                                        Attachments
                                    </h5>
                                    <div className={"select-attribute-wrapper"}>
                                        <div className={"select-attribute-body"}>
                                            {this.state.files.length > 0 ? null : (
                                                <div className={"d-flex justify-content-center"}>
                                                    <p style={{fontWeight: 100}} className={"my-4"}>
                                                        Add your Attachments
                                                    </p>
                                                </div>
                                            )}
                                            {this._renderAttachments()}
                                        </div>
                                        <div className={"select-attribute-footer mt-2 d-flex justify-content-end"}>
                                            <Button theme="primary" onClick={this.toggleAttachment}>Add</Button>
                                        </div>
                                    </div>
                                </div>
                                <hr className={"m-0"}/>
                                <div className={"btn-wrapper d-flex justify-content-between p-3"}>
                                    <button type="submit" className="btn btn-success-custom align-self-end"
                                            onClick={this.props.goToPrevSlide}>
                                        <IoIosArrowDropleftCircle className={"mr-2 font-size-19"}
                                                                  style={{"verticalAlign": "-4px"}}/>
                                        <span>
                                    Back
                                </span>
                                    </button>
                                    <button type="submit" className="btn btn-success-custom align-self-end"
                                            onClick={this.checkFormValidation}>
                                        <span>
                                            Proceed
                                        </span>
                                        <IoIosArrowDroprightCircle className={"ml-2 font-size-19"}/>
                                    </button>
                                </div>
                            </div>
                        </Fragment>
                    ) : null
                }
            </Fragment>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        userFollowings: state.userFollowings,
        allAttributes: state.allAttributes,
        sessionID: state.session.data.username
    }
};
export default connect(mapStateToProps, null)(StepTwo);