                                <path
                                    d="m419.320312 90.015625c2.242188 2.429687 6.027344 2.589844 8.464844.355469l31.898438-29.21875c1.597656-1.445313 2.296875-3.632813 1.835937-5.738282-.460937-2.101562-2.015625-3.796874-4.066406-4.4375-2.054687-.644531-4.296875-.136718-5.875 1.328126l-27.492187 25.179687-12.789063-13.871094c-2.246094-2.4375-6.042969-2.589843-8.480469-.34375-2.433594 2.246094-2.589844 6.042969-.34375 8.476563zm0 0"/>
