import React from "react";

export const Stats = ({
                          currentStep,
                          firstStep,
                          goToStep,
                          lastStep,
                          nextStep,
                          previousStep,
                          totalSteps,
                          step,
                      }) => (
    <div>
        <div className={"d-flex justify-content-end my-3"}>
            {step > 1 &&
            <button className='new-decision-button btn-success-custom btn btn-success mx-2' onClick={previousStep}>Go
                Back</button>
            }
            {step < totalSteps ?
                <button className='new-decision-button btn-success-custom btn btn-success mx-2' onClick={nextStep}>
                    Continue</button>
                :
                <button className='new-decision-button btn-success-custom btn btn-success mx-2' onClick={nextStep}>
                    Finish</button>
            }
        </div>
    </div>
);