import React, {Component} from "react";
import {FaLock, FaUser, FaAt} from "react-icons/fa";
import {Redirect} from "react-router";
import "../App.css";
import toast from 'toasted-notes'
import 'toasted-notes/src/styles.css';
import {PulseLoader} from 'react-spinners';
import {css} from '@emotion/core';
import {IoMdCloseCircle, IoMdCheckmarkCircle} from "react-icons/io";

const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
`;


class SignForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            activeTab: true,
            loginUserName: "",
            loginPassword: "",
            gotError: false,
            isLoading: false,
            isLoggedIn: false,
            signUpUserName: "",
            signUpRepeatPassword: "",
            signUpEmail: "",
            signUpPassword: "",
            isLoadingToSignUp: false
        }
    }

    componentDidMount() {
        if (localStorage.getItem('authToken')) {
            let username = localStorage.getItem("user");
            this.setState({
                isLoggedIn: true,
                loginUserName: username
            })
        }
    }


    showErrorMessage = (text) => toast.notify(
        <p className={"m-0 p-0 font-size-14"}>
            <span className={"mr-2 font-size-19"} style={{verticalAlign: "1px"}}>
                <IoMdCloseCircle style={{color: "#f5222d"}}/>
            </span>
            {text}
        </p>
        , {
            duration: 2000
        });

    showSuccessMessage = (text, type) => toast.notify(
        <p className={"m-0 p-0 font-size-14"}>
            <span className={"mr-2 font-size-19"} style={{verticalAlign: "1px"}}>
                    <IoMdCheckmarkCircle style={{color: "#52c41a"}}/>
            </span>
            {text}
        </p>
        , {
            duration: 2000
        });

    toggle = () => {
        this.setState({
            activeTab: !this.state.activeTab
        })
    };

    signUpUser = async () => {
        this.setState({
            isLoadingToSignUp: true
        });
        if (this.state.signUpUserName.length === 0 ||
            this.state.signUpRepeatPassword.length === 0 ||
            this.state.signUpEmail.length === 0 ||
            this.state.signUpPassword.length === 0
        ) {
            this.setState({
                isLoadingToSignUp: false
            });
            return this.showErrorMessage("Please fill all field.");
        }
        if (this.state.signUpPassword !== this.state.signUpRepeatPassword) {
            this.setState({
                isLoadingToSignUp: false
            });
            return this.showErrorMessage("Password does not match.");
        }

        let requestBody = {
            "email": this.state.signUpEmail,
            "password": this.state.signUpPassword,
            "username": this.state.signUpUserName,
        };

        let options = {
            method: 'POST',
            body: JSON.stringify(requestBody),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        };

        await fetch(`https://dxghost.pythonanywhere.com/users/`, options)
            .then(res => {
                if (res.status === 400) {
                    this.setState({
                        gotError: true
                    });
                    this.showErrorMessage("A user with that username already exists");
                } else if (res.status === 201) {
                    this.setState({
                        gotError: false
                    });
                    res.json().then(data => {
                        this.setState({
                            activeTab: !this.state.activeTab
                        });
                        this.showSuccessMessage("Your account created successfully");
                    });
                }
            });

        this.setState({
            isLoadingToSignUp: false
        });
    };

    loginUser = async () => {
        if (this.state.loginUserName.length === 0 ||
            this.state.loginPassword.length === 0) {
            return this.showErrorMessage("Please fill all field");
        }
        this.setState({
            gotError: false,
            isLoading: true
        });
        let requestBody = {
            "password": this.state.loginPassword,
            "username": this.state.loginUserName
        };
        let options = {
            method: 'POST',
            body: JSON.stringify(requestBody),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        };

        await fetch(`https://dxghost.pythonanywhere.com/api-token-auth/`, options)
            .then(res => {
                if (res.status === 400) {

                    this.setState({
                        gotError: true
                    });
                    this.showErrorMessage("Invalid Username/Password");
                } else if (res.status === 200) {
                    this.setState({
                        gotError: false
                    });
                    res.json().then(data => {
                        localStorage.setItem("authToken", data.token);
                        localStorage.setItem("user", this.state.loginUserName);
                        this.checkLoginValidating();
                        this.showSuccessMessage("login successfully");
                    });
                }
            });


        this.setState({
            isLoading: false
        });
        if (this.state.gotError) {
            return null
        }
    };

    checkLoginValidating = () => {
        if (localStorage.getItem('authToken')) {
            this.setState({
                isLoggedIn: true
            });
            window.location.reload();
        }
    };

    _renderLogin = () => {
        return (
            <div className={"col-12 col-sm-12 col-md-8 col-lg-6"}>
                <div className="card">
                    <article className="card-body">
                        <h4 className="card-title text-center mb-4 mt-1">Login to your account</h4>
                        <hr/>
                        {/*<p className="text-success text-center">Some message goes here</p>*/}
                        <form>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                                <span className="input-group-text">
                                                    <FaUser/>
                                                </span>
                                    </div>
                                    <input
                                        value={this.state.loginUserName}
                                        onChange={(e) => this.setState({loginUserName: e.target.value})}
                                        className="form-control" placeholder="Username"
                                        type="email">
                                    </input>
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                                <span className="input-group-text">
                                                    <FaLock/>
                                                </span>
                                    </div>
                                    <input
                                        value={this.state.loginPassword}
                                        onChange={(e) => this.setState({loginPassword: e.target.value})}
                                        className="form-control" placeholder="Password" type="password">

                                    </input>
                                </div>

                            </div>

                            <div className="form-group">
                                <button
                                    onClick={this.loginUser}
                                    type={"button"}
                                    className="btn btn-primary btn-block">{this.state.isLoading ? (
                                    <div className='sweet-loading'>
                                        <PulseLoader
                                            css={override}
                                            sizeUnit={"px"}
                                            color={"white"}
                                            size={7}
                                            loading={true}
                                        />
                                    </div>) : "Login"}
                                </button>
                            </div>

                            <div className="form-group">
                                <button type={"button"} onClick={this.toggle} className="btn btn-default btn-block">
                                    Don't have an account ?
                                </button>
                            </div>
                        </form>
                    </article>
                </div>
            </div>
        )
    };

    _renderSignUp = () => {
        return (
            <div className={"col-12 col-sm-12 col-md-8 col-lg-6"}>
                <div className="card">
                    <article className="card-body">
                        <h4 className="card-title text-center mb-4 mt-1">Create account</h4>
                        <hr/>
                        {/*<p className="text-success text-center">Some message goes here</p>*/}
                        <form>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                                <span className="input-group-text">
                                                    <FaUser/>
                                                </span>
                                    </div>
                                    <input
                                        value={this.state.signUpUserName}
                                        className="form-control" placeholder="Username"
                                        onChange={(e) => this.setState({signUpUserName: e.target.value})}
                                        type="username">
                                    </input>
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                                <span className="input-group-text">
                                                    <FaAt/>
                                                </span>
                                    </div>
                                    <input
                                        value={this.state.signUpEmail}
                                        className="form-control" placeholder="Email"
                                        onChange={(e) => this.setState({signUpEmail: e.target.value})}
                                        type="email">
                                    </input>
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                                <span className="input-group-text">
                                                    <FaLock/>
                                                </span>
                                    </div>
                                    <input
                                        value={this.state.signUpPassword}
                                        onChange={(e) => this.setState({signUpPassword: e.target.value})}
                                        className="form-control" placeholder="Password" type="password">
                                    </input>
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                                <span className="input-group-text">
                                                    <FaLock/>
                                                </span>
                                    </div>
                                    <input
                                        value={this.state.signUpRepeatPassword}
                                        onChange={(e) => this.setState({signUpRepeatPassword: e.target.value})}
                                        className="form-control" placeholder="Password Confirmation"
                                        type="password">

                                    </input>
                                </div>
                            </div>
                            <div className="form-group">
                                <button
                                    onClick={this.signUpUser}
                                    type={"button"} className="btn btn-primary btn-block">

                                    {this.state.isLoadingToSignUp ? (
                                        <div className='sweet-loading'>
                                            <PulseLoader
                                                css={override}
                                                sizeUnit={"px"}
                                                color={"white"}
                                                size={7}
                                                loading={true}
                                            />
                                        </div>) : "Sign Up"}
                                </button>
                            </div>
                            <div className="form-group">
                                <button type={"button"} onClick={this.toggle} className="btn btn-default btn-block">
                                    Already have an account ?
                                </button>
                            </div>
                        </form>
                    </article>
                </div>
            </div>
        )
    };

    render() {
        // if (this.state.isLoggedIn) {
        //     return <Redirect to={`/`}/>
        // }
        return (
            <div className={"container justify-content-center"} style={{height: "100%"}}>
                <div className={"row justify-content-center"}>
                    {this.state.activeTab ? this._renderLogin() : this._renderSignUp()}
                </div>
            </div>
        )
    }
}

export default SignForm

