import React, {Component} from "react";
import avatarProf from "../../assets/Profile.jpg";
import {FaUserCircle, FaSignOutAlt} from "react-icons/fa";
import navbarBrandImg from "../../assets/icon.png";
import {Redirect} from "react-router";
import {connect} from "react-redux";
import {MdMenu} from "react-icons/md";
import {withRouter} from "react-router";
import {IoMdListBox, IoIosAnalytics, IoMdContact, IoIosSettings, IoIosLogOut} from "react-icons/io";
import {NavLink as NavLinkRouter} from "react-router-dom";
import {
    Navbar,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    Collapse,
    Button
} from "shards-react";

class NavbarUI extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdownOpenWithoutText: false,
            dropdownOpenWithText: false,
            collapseOpen: false,
            isLoggedOut: false
        };
    }

    toggleDropdownWithText = () => {
        this.setState({
            ...this.state,
            ...{
                dropdownOpenWithText: !this.state.dropdownOpenWithText
            }
        });
    };

    toggleDropdownWithoutText = () => {
        this.setState({
            ...this.state,
            ...{
                dropdownOpenWithoutText: !this.state.dropdownOpenWithoutText
            }
        });
    };

    toggleNavbar = () => {
        this.setState({
            ...this.state,
            ...{
                collapseOpen: !this.state.collapseOpen
            }
        });
    };

    logout = () => {
        localStorage.removeItem('authToken');
        this.setState({
            isLoggedOut: true
        });
        window.location.reload()
    };

    render() {

        return (
            <Navbar type="dark" theme="primary" expand="md" className={"navbar-ui-custom"}>
                <Button onClick={this.toggleNavbar} className={"navbar-toggler navbar-toggeler-icon-custom"}>
                    <MdMenu className={"text-white"}/>
                </Button>
                <NavbarBrand href="#" className={"navbar-brand-ui-icon"}>
                    <img src={navbarBrandImg} className={"navbar-brand-ui-icon-img"}/>
                </NavbarBrand>

                <Dropdown
                    open={this.state.dropdownOpenWithoutText}
                    toggle={this.toggleDropdownWithoutText}
                    className={"mr-2 dropdown-menu-right hide-in-768"}
                >
                    <DropdownToggle nav caret className={"text-white m-0 p-0 font-size-11"}>
                            <span className={"nav-profile-avatar"}>
                                <img src={avatarProf}/>
                                <div className={"avatar-green-spot"}>
                                </div>
                            </span>
                    </DropdownToggle>
                    <DropdownMenu right className={"dropdown-menu-ui-custom"}>
                        <NavLinkRouter
                            to={"/people/users/dx"}
                            className={"profile-item-"}
                            activeClassName={"active"}>
                            <DropdownItem>
                                <IoMdContact className={"mr-2 font-size-18"} style={{verticalAlign: "-3px"}}/>
                                Profile
                            </DropdownItem>
                        </NavLinkRouter>
                        <NavLinkRouter
                            to={"/setting"}
                            className={"profile-item-"}
                            activeClassName={"active"}>
                            <DropdownItem>
                                <IoIosSettings className={"mr-2 font-size-18"} style={{verticalAlign: "-3px"}}/>
                                Setting
                            </DropdownItem>
                        </NavLinkRouter>
                        <DropdownItem onClick={this.logout}>
                            <IoIosLogOut className={"mr-2 font-size-18"} style={{verticalAlign: "-3px"}}/>
                            Logout
                        </DropdownItem>
                    </DropdownMenu>
                </Dropdown>

                <Collapse open={this.state.collapseOpen} navbar>
                    <Nav navbar>
                        <NavItem className={"mx-0 mx-md-1 mt-3 mt-md-0 nav-item-ui-custom"}>

                            <NavLinkRouter
                                className={"py-2 d-block text-white px-1 px-md-4 font-size-13"}
                                activeClassName={"navbar-ui-item-active p-2 d-block text-white px-1 px-md-4 font-size-13"}
                                to={"/decision"}>
                                <span className={"mr-2 font-size-14"}>
                                    <IoMdListBox className={"navbar-item-icon"}/>
                                </span>
                                Decision
                            </NavLinkRouter>
                        </NavItem>
                        <NavItem className={"mx-0 mx-md-1 mt-3 mt-md-0 nav-item-ui-custom"}>
                            <NavLinkRouter
                                className={"py-2 d-block text-white px-1 px-md-4 font-size-13"}
                                activeClassName={"navbar-ui-item-active p-2 d-block text-white px-1 px-md-4 font-size-13"}
                                to={"/attribute"}>
                                <span className={"mr-2 font-size-14"}>
                                    <IoIosAnalytics className={"navbar-item-icon text-white"}/>
                                </span>

                                Attribute
                            </NavLinkRouter>
                        </NavItem>
                        <NavItem className={"mx-0 mx-md-1 mt-3 mt-md-0 nav-item-ui-custom"}>
                            <NavLinkRouter
                                className={"py-2 d-block text-white px-1 px-md-4 font-size-13"}
                                activeClassName={"navbar-ui-item-active p-2 d-block text-white px-1 px-md-4 font-size-13"}
                                to={"/people"}>
                                <span className={"mr-2 font-size-14"}>
                                    <FaUserCircle className={"navbar-item-icon text-white"}/>
                                </span>
                                People
                            </NavLinkRouter>
                        </NavItem>
                    </Nav>

                </Collapse>

                <Nav navbar className="ml-auto hide-under-768 align-items-center">
                    <NavItem className={"nav-item-ui-custom navbar-item-right mr-3"}>
                        <NavLink href="#" className={"py-2 d-block px-1 px-md-4 font-size-13"}>
                            Invite Friends
                        </NavLink>
                    </NavItem>
                    <Dropdown
                        open={this.state.dropdownOpenWithText}
                        toggle={this.toggleDropdownWithText}
                        className={"mr-2 dropdown-menu-right"}
                    >
                        <DropdownToggle nav caret className={"text-white m-0 p-0 font-size-11 "}>
                            <span className={"nav-profile-avatar"}>
                                <img src={avatarProf}/>
                                <div className={"avatar-green-spot"}>
                                </div>
                            </span>
                            <span className={"ml-2 text-white"}>
                                Uwe Hesterman
                            </span>
                        </DropdownToggle>
                        <DropdownMenu right className={"dropdown-menu-ui-custom"}>
                            <NavLinkRouter
                                to={`/people/users/${this.props.sessionID}`}
                                className={"profile-item-"}
                                activeClassName={"active"}>
                                <DropdownItem>
                                    <IoMdContact className={"mr-2 font-size-18"} style={{verticalAlign: "-3px"}}/>
                                    Profile
                                </DropdownItem>
                            </NavLinkRouter>
                            <NavLinkRouter
                                to={"/setting"}
                                className={"profile-item-"}
                                activeClassName={"active"}>
                                <DropdownItem>
                                    <IoIosSettings className={"mr-2 font-size-18"} style={{verticalAlign: "-3px"}}/>
                                    Setting
                                </DropdownItem>
                            </NavLinkRouter>
                            <DropdownItem onClick={this.logout}>
                                <IoIosLogOut className={"mr-2 font-size-18"} style={{verticalAlign: "-3px"}}/>
                                Logout
                            </DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </Nav>
            </Navbar>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        sessionID: state.session.data.username
    }
};

export default withRouter(connect(mapStateToProps, null)(NavbarUI));