import React, {Component, Fragment} from "react";
import {withRouter, Switch, Route} from "react-router";
import {Link} from "react-router-dom";
import {Button} from "shards-react";
import {connect} from "react-redux";
import {fetchDecisionAlternativesById, fetchDecisionById} from "../actions/index";
import {ScaleLoader} from 'react-spinners';
import {css} from '@emotion/core';
import {IoIosAttach} from "react-icons/io";

const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
`;


class Decision extends Component {


    componentDidMount = () => {
        const {dispatch, match} = this.props;
        dispatch(fetchDecisionAlternativesById(match.params["id"]));
        dispatch(fetchDecisionById(match.params["id"]));

    };

    createMarkup = (text) => {
        return {__html: text};
    };


    _renderData = () => {

        return (
            <div className={"poll-wrapper"} style={{backgroundColor: "#fafafa"}}>
                <div className={"poll-data-wrapper container"}>
                    <div className={"poll-data-header m-3 m-md-5 d-flex justify-content-between"}>
                        <div>
                            <h3 className={"poll-data-header-title"}>
                                {this.props.data["name"]}
                            </h3>
                            <small className={"text-muted"}>
                                Created by
                                <Link to={`/people/users/${this.props.data["created_by"]}`}
                                      className={"ml-2 poll-data-header-creator"}>
                                    @{this.props.data["created_by"]}
                                </Link>
                            </small>
                        </div>
                        <div>
                            <small className={"text-muted font-size-10"}>
                                {String(this.props.data["pub_date"]).substring(0, 10)}
                            </small>
                        </div>
                    </div>
                    <hr/>
                    <div className={"poll-data-body mx-3 mx-md-5"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Question:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                <h6 className={"font-size-14"}>
                                    {this.props.data["question"]}
                                </h6>
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    State:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                <h6 className={"status-custom-badge font-size-14"}>
                                    {this.props.data["state"]}
                                </h6>
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Publish Date:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                <h6 className={"font-size-14"}>
                                    {this.props.data["pub_date"]}
                                </h6>
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Finish Date:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                <h6 className={"font-size-14"}>
                                    {this.props.data["finish_date"]}
                                </h6>
                            </div>
                        </div>
                    </div>
                    <hr/>
                    <div className={"poll-data-body mx-3 mx-md-5"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Problem Opportunity:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                <h6 className={"problem-details font-size-14"}>
                                    <div
                                        dangerouslySetInnerHTML={this.createMarkup(this.props.data["problem_opportunity"])}>

                                    </div>
                                </h6>
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Problem Objective:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                <h6 className={"problem-details font-size-14"}>
                                    <div
                                        dangerouslySetInnerHTML={this.createMarkup(this.props.data["problem_objective"])}>

                                    </div>
                                    {/*{this.props.data["problem_objective"]}*/}
                                </h6>
                            </div>
                        </div>
                    </div>
                    <hr/>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Decision Alternatives:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                {this.props.alternatives.map((item, index) => {
                                    return (
                                        <span className={"attribute-badge"} key={index}>
                                            {item["name"]}
                                        </span>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Required Attributes:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                {Object.keys(this.props.data).length > 0 ? (
                                    this.props.data["attributes"].map((item, index) =>
                                        <Link to={`/attribute/t/${item}`} key={index}>
                                            <span className={"attribute-badge"}>
                                                {item}
                                            </span>
                                        </Link>
                                    )
                                ) : <div></div>}
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Participants:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                {Object.keys(this.props.data).length > 0 ? (
                                    this.props.data["participants"].map((item, index) =>
                                        <Link to={`/people/users/${item}`} key={index}>
                                            <span className={"attribute-badge"}>
                                                {item}
                                            </span>
                                        </Link>
                                    )
                                ) : <div></div>}
                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-2"}>
                        <div className={"row"}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-13"}>
                                    Attachment:
                                </h6>
                            </div>
                            <div className={"col-12 col-md-9"}>
                                {this.props.data["attachment"] ? (
                                    <div className={"attachment-ui d-flex"}>
                                        <div className={"p-3 attachment-ui-icon"}>
                                            <IoIosAttach className={"font-size-20"}/>
                                        </div>
                                        <div className={"p-4"}>
                                            <h6 className={"font-size-14"}>
                                                {this.props.data["attachment"]}
                                            </h6>
                                        </div>
                                    </div>
                                ) : (<p className={"attachment-placeholder font-size-14"}>There is no attachment to this
                                    decision</p>)}

                            </div>
                        </div>
                    </div>
                    <div className={"poll-data-body mx-3 mx-md-5 my-3 mb-5 d-flex justify-content-end"}>
                        <Link to={`/decision/polls/`}>
                            <Button className={"btn mr-3 px-5"}>
                                Reject
                            </Button>
                        </Link>
                        <Link to={`/decision/polls/${this.props.match.params["id"]}/voting`}>
                            <Button className={"btn px-5 new-decision-button btn-success-custom btn btn-success"}>
                                Start Voting
                            </Button>
                        </Link>
                    </div>
                </div>
            </div>
        )
    };

    _renderSpinnerOrData = () => {
        if (!this.props.isFetching) {
            return this._renderData()
        }
        if (this.props.isFetching) {
            return (
                <div className={"poll-sppiner-wrapper"}>
                    <div className='sweet-loading'>
                        <ScaleLoader
                            css={override}
                            sizeUnit={"px"}
                            size={150}
                            color={'#FF8260'}
                            loading={this.props.isFetching}
                        />
                    </div>
                </div>
            )
        }
    };

    render() {
        return (
            <Fragment>
                {this._renderSpinnerOrData()}
            </Fragment>
        )
    }
}

const mapStateToProps = (state) => {
    const {data, getError, isFetching, textError, alternatives} = state.selectedDecision;
    return {
        data, getError, isFetching, textError, alternatives
    }
};

export default withRouter(connect(mapStateToProps)(Decision));