import React, {Component} from "react";
import {withRouter} from "react-router"
import {connect} from "react-redux";
import {Slider} from "shards-react";
import {Badge} from "shards-react";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import {PulseLoader, ScaleLoader} from 'react-spinners';
import {css} from '@emotion/core';
import {fetchDecisionAlternativesById} from "../../../actions";
import {fetchVotingStep3} from "../../../actions/index";
import {authHeader} from "../../../actions/AuthHeader";

const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
`;

class ThirdStep extends Component {
    constructor(props) {
        super(props);
        this.state = {
            step: 3,
            value: {},
            isPosting: false,
            isVoted: false,
            gotError: false,
            loadingSlider: false
        };
        this.modules = {
            toolbar: [
                [{'header': [1, 2, 3, 4, 5, 6, false]}],
                ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                [{'script': "super"}, {'script': "sub"}],
                [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
                [{"align": []}, "direction"],
                ['link'],
                ['clean']
            ],
        };
        this.formats = [
            'header',
            'align',
            'script',
            'bold', 'italic', 'underline', 'strike', 'blockquote',
            'list', 'bullet', 'indent',
            'link', 'image'
        ]

    }

    _renderContinueBtn = () => {
        return (
            <button className='new-decision-button btn-success-custom btn btn-success mx-2'
                    onClick={this.submitVote}>
                {this.state.isPosting ? (<div className='sweet-loading'>
                    <PulseLoader
                        css={override}
                        sizeUnit={"px"}
                        color={"white"}
                        size={7}
                        loading={true}
                    />
                </div>) : "Finish"}
            </button>
        )
    };

    async componentDidMount() {
        this.setState({
            loadingSlider: true
        });
        const {dispatch, match} = this.props;
        await dispatch(fetchDecisionAlternativesById(this.props.match.params["id"]));
        await dispatch(fetchVotingStep3(match.params["id"]));
        const {alternatives} = this.props;
        const votedList = this.props.ThirdStep["data"];

        if (votedList.length > 0) {
            this.setState({
                isVoted: true
            })
        }

        let a = {};
        if (this.state.isVoted) {
            alternatives.map((item, index) => {
                return a[item["name"]] = votedList[index]["rate"];
            });
        } else {
            alternatives.map(item => {
                return a[item["name"]] = 4;
            });
        }
        this.setState({
            value: a,
            loadingSlider: false
        });
    }

    submitVote = async () => {
        this.setState({
            gotError: false
        });

        if (this.state.isVoted) {
            this.setState({
                gotError: false
            });
            this.setState({
                isPosting: true
            });
            let postData;
            postData = this.props.alternatives.map((item) => {
                return {
                    "rate": this.state.value[item["name"]],
                    "alternative": item["id"],
                }
            });

            const votedList = this.props.ThirdStep["data"];

            for (let i = 0; i < postData.length; i++) {
                await fetch(`https://dxghost.pythonanywhere.com/alternativevotes/${votedList[i]["id"]}/`, {
                    method: 'PUT',
                    body: JSON.stringify(postData[i]),
                    headers: new Headers({
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'Authorization': authHeader()
                    })
                })
                    .then(res => {
                        if (res.status === 400) {
                            this.setState({gotError: true})
                        }
                    })

            }

            this.setState({
                isPosting: false
            });
            if (this.state.gotError) {
                return null;
            }
            return this.props.history.push("/decision/polls")
        }

        this.setState({
            gotError: false
        });
        this.setState({
            isPosting: true
        });
        let postData;
        postData = this.props.alternatives.map((item) => {
            return {
                "rate": this.state.value[item["name"]],
                "alternative": item["id"],
            }
        });


        await fetch(`https://dxghost.pythonanywhere.com/alternativevotes/`, {
            method: 'POST',
            body: JSON.stringify(postData),
            headers: new Headers({
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': authHeader()
            })
        })
            .then(res => {
                if (res.status === 400) {
                    this.setState({gotError: true})
                }
            })

        this.setState({
            isPosting: false
        });
        if (this.state.gotError) {
            return null;
        }
        return this.props.history.push("/decision/polls")
    };

    render() {

        return (
            <div className={"mx-3 mx-md-5"}>
                <h3 className={"fw-600"}>
                    Alternatives
                </h3>
                <h6 className={"fw-600"}>
                    Vote for your overall evaluation of each alternatives
                </h6>
                <hr/>
                <div className={"row align-items-center"}>
                    <div className={"col-12 col-md-3"}>
                        <h6 className={"text-muted fw-600 font-size-13 m-0 p-0"}>
                            Alternative
                        </h6>
                    </div>
                    <div className={"col-12 col-md-8"}>
                        <h6 className={"text-muted fw-600 font-size-13 m-0 p-0"}>
                            Your evaluation
                        </h6>
                    </div>
                </div>
                {!this.state.loadingSlider ?
                    (this.props.alternatives.length > 0 ? this.props.alternatives.map((item, index) => {
                        return (<div className={"row align-items-center"} key={index}>
                            <div className={"col-12 col-md-3"}>
                                <h6 className={"fw-600 font-size-18 m-0 p-0"}>
                                    <Badge pill>{item["name"]}</Badge>
                                </h6>
                            </div>
                            <div className={"col-12 col-md-8"}>
                                <Slider
                                    onSlide={(e) => {
                                        let newValue = {...this.state.value};
                                        newValue[item["name"]] = parseFloat(e[0]);
                                        this.setState({
                                            value: newValue
                                        })
                                    }}
                                    connect={[true, false]}
                                    step={1}
                                    start={[this.state.value[item["name"]]]}
                                    range={{min: 1, max: 9}}/>
                            </div>
                            <div className={"col-12 col-md-1"}>
                                <h6 className={"fw-600 font-size-13 m-0 p-0"}>
                                    {this.state.value[item["name"]]} / 9
                                </h6>
                            </div>
                        </div>)
                    }) : null) :
                    (
                        <div className={"d-flex justify-content-center"}>
                            <div className='sweet-loading'>
                                <ScaleLoader
                                    css={override}
                                    sizeUnit={"px"}
                                    size={150}
                                    color={'#FF8260'}
                                    loading={true}
                                />
                            </div>
                        </div>
                    )
                }
                <hr/>
                <h4>
                    Your comment about this decision (optional):
                </h4>
                <div className={"editor fw-300"}>
                    <ReactQuill theme="snow"
                                modules={this.modules}
                                formats={this.formats}>
                    </ReactQuill>
                </div>
                <hr/>
                <div>
                    <div className={"d-flex justify-content-end my-3"}>
                        {this.state.step > 1 &&
                        <button className='new-decision-button btn-success-custom btn btn-success mx-2'
                                onClick={this.props.previousStep}>Go
                            Back</button>
                        }
                        {this._renderContinueBtn()}
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        ThirdStep: state.votingStep.ThirdStep,
        alternatives: state.selectedDecision["alternatives"]
    }
};

export default withRouter(connect(mapStateToProps, null)(ThirdStep));