import React, {Component} from "react";
import {withRouter, Switch, Route, Redirect} from "react-router";
import {connect} from "react-redux";

class Attribute extends Component {
    render() {
        return (
            <Switch>
                <Route exact path="/attribute" render={() => <Redirect to="/attribute/t"/>}/>
                <Route exact path={"/attribute/t"} render={() => <div>Attribute</div>}/>
                <Route exact path={"/attribute/make"} render={() => <div>Make new Attribute</div>}/>
            </Switch>
        )
    }
}

export default withRouter(connect()(Attribute));