import React, {Component} from "react";
import {withRouter, Switch} from "react-router";
import {NavLink} from "react-router-dom";

class SubmenuAttribute extends Component {
    render() {
        return (
            <div className={"submenu-items font-size-13"}>
                <NavLink className={"submenu-item hvr-sweep-to-top"} activeClassName={"submenu-item-active"}
                         to={"/attribute/t"}>
                    Attributes
                </NavLink>
                <NavLink className={"submenu-item hvr-sweep-to-top"} activeClassName={"submenu-item-active"}
                         to={"/attribute/make"}
                         exact>
                    Make New Attribute
                </NavLink>

            </div>
        )
    }
}


export default withRouter(SubmenuAttribute);